import jjOrderItem from '../../components/jj-order-item.vue'
import jjOrderGoods from '../../components/jj-order-goods.vue'
export default {
	data() {
		return {
			scrollLeft: 0,
			tabIndex: 0,
			tabBars: [{
				name: '工商类',
				id: 'gongshanglei'
			}, {
				name: '代账类',
				id: 'daizhanglei'
			}, {
				name: '产品类',
				id: 'chanpinlei'
			}, {
				name: '商标类',
				id: 'shangbiaolei'
			}]
		}
	},
	components:{
		jjOrderItem,
		jjOrderGoods
	},
	onLoad(e) {
		this.tabIndex = e.index;
	},
	methods: {
		async changeTab(e) {
			let index = e.target.current;
			this.tabIndex = index; //一旦访问data就会出问题
		},
		async tapTab(e) { //点击tab-bar
			let tabIndex = e.target.dataset.current;
			this.tabIndex = tabIndex;
		},
		goNewLogin(){
			uni.navigateTo({
				url:'../new_login/new_login'
			})
		},
		goOrder(){
			uni.navigateTo({
				url:'../order_dzl/order_dzl'
			})
		},
		orderGoodsInfo(){
			uni.navigateTo({
				url:'../order_goods_info/order_goods_info'
			})
		},
		orderStatus(index){
			uni.navigateTo({
				url:'../order_types/order_types?index='+index
			})
		},
		goBrankOrder(){
			uni.navigateTo({
				url:'../order_sbl/order_sbl'
			})
		}
	}
}
